<template>
  <v-navigation-drawer
    mini-variant
    absolute
    expand-on-hover
    :temporary="core.mobile && !!main"
    :permanent="permanent && opened"
    v-model="opened"
    :src="background"
    :app="main"
    :clipped="main"
  >
  <div style="background-color: rgba(0,0,20,0.7); width: 100%; height: 100%">
    <v-list-item two-line v-if="api.userInfo" class="px-2">
      <v-list-item-avatar><v-img :src="`https://my.msk.host${api.userInfo.avatar}`"/></v-list-item-avatar>
      <v-list-item-content>
        <v-list-item-title>{{api.userInfo.realname}}</v-list-item-title>
        <v-list-item-subtitle>{{api.userInfo.name}}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <v-divider/>
    <v-list nav dense>
      <MenuItem v-for="(item, key) in menu" :key="key" :item="item"/>
    </v-list>
  </div>
  </v-navigation-drawer>
</template>

<script>
import MenuItem from '@/render/components/Common/SideBarMenuItem';

const menu = [
  {
    type: 'item',
    label: 'Главная',
    icon: 'mdi-home',
    tab: 'Overview'
  },
  {
    type: 'item',
    label: 'Тикеты',
    icon: 'mdi-alpha-t-circle-outline',
    tab: 'Tickets'
  },
  {},
  {
    type: 'group',
    label: 'Фильтры',
    icon: 'mdi-buffer',
    list: [
      { type: 'item', label: 'Тикеты', tab: 'AllTickets' },
      { type: 'item', label: 'Услуги', tab: 'Services' }
    ]
  },
  {
    type: 'group',
    label: 'Открыть',
    icon: 'mdi-balloon',
    list: [
      { type: 'component', label: 'Услугу', component: 'OpenServiceById' },
      { type: 'component', label: 'Тикет', component: 'FastOpenTicket' },
      { type: 'component', label: 'Аккаунт', component: 'FastOpenAccount' }
    ]
  },
  {
    type: 'group',
    label: 'Утилиты',
    icon: 'mdi-calculator',
    list: [
      { type: 'item', label: 'Калькулятор возвратов', tab: 'RefundCalculator' },
      { type: 'item', label: 'Калькулятор смены срока аренды', tab: 'RedateCalculator' }
    ]
  },
  {},
  {
    type: 'item',
    label: 'Сотрудники',
    icon: 'mdi-account-group',
    tab: 'Staff'
  },
  {
    type: 'item',
    label: 'Настройки',
    icon: 'mdi-cog',
    tab: 'Settings'
  },
  {},
  {
    type: 'item',
    label: 'Разработчику',
    icon: 'mdi-dev-to',
    click: v => {
      v.platform.openDevTools();
    }
  }
];

export default {
  components: { MenuItem },
  props: ['main', 'value'],
  data: () => ({ menu, opened: false }),

  mounted() {
    this.opened = this.permanent;
  },

  computed: {
    background() {
      return localStorage.background;
    },

    permanent() {
      return !this.main || !this.core.mobile;
    }
  },

  watch: {
    opened(val) {
      if (!val && this.permanent) {
        setTimeout(() => { this.opened = true; }, 1);
      }
    }
  }
};
</script>
