const cache = {};

export default {
  props: ['id'],
  abstract: true,

  render() {
    const vnode = this.$slots.default[0];
    if (!vnode) {
      return;
    }

    if (cache[this.id] && cache[this.id].componentInstance) {
      // return cache[this.id];
      vnode.componentInstance = cache[this.id].componentInstance;
    }

    cache[this.id] = vnode;
    return vnode;
  }
};
