<template>
<div style="height: 100%">
  <!-- Right zone -->
  <v-navigation-drawer :stateless="!core.mobile" clipped app width="300" v-if="$slots.leftSidebar" :value="!core.mobile">
    <ReusedComponent id="sidebar"><SideBar :value="true"/></ReusedComponent>
    <div style="padding-left: 56px"><slot name="leftSidebar"/></div>
  </v-navigation-drawer>
  <ReusedComponent id="sidebar-main" v-else><SideBar :main="true" :value="!core.mobile"/></ReusedComponent>
  <!-- Left zone -->
  <v-navigation-drawer :stateless="!core.mobile" app clipped right v-if="$slots.rightSidebar" :value="!core.mobile">
    <slot name="rightSidebar"/>
  </v-navigation-drawer>
  <!-- Middle zone -->
  <transition appear name="slide-fade" mode="out-in">
    <v-main :style="`height: 100vh; padding-bottom: ${footerHeight ? `${footerHeight}px` : '0px'}; ${style}`">
      <slot/>
    </v-main>
  </transition>
  <v-footer app absolute height="auto" inset ref="footer" v-if="$slots.footer">
    <slot name="footer"/>
  </v-footer>
</div>
</template>

<script>
import SideBar from '@/render/components/Common/NormalSideBar.vue';
import ReusedComponent from '@/render/components/Utils/ReusedComponent';

export default {
  components: { SideBar, ReusedComponent },
  props: ['background'],
  data: () => ({
    footerHeight: 0
  }),

  mounted() {
    if (!this.$refs.footer) {
      return;
    }

    const el = this.$refs.footer.$el;
    const resizeObserver = new ResizeObserver(() => {
      this.footerHeight = el.clientHeight;
    });

    resizeObserver.observe(el);
    this.footerHeight = el.clientHeight;
  },

  computed: {
    style() {
      const url = this.background || localStorage.background;
      return url ? `background-size: cover; background-image: url(${url})` : '';
    }
  }
};
</script>
