<template>
  <v-list-item v-if="item.type === 'item'" @click="click">
    <v-list-item-icon v-if="item.icon"><v-icon>{{item.icon}}</v-icon></v-list-item-icon>
    <v-list-item-title>{{item.label}}</v-list-item-title>
  </v-list-item>
  <v-list-group v-else-if="item.type === 'group'">
    <template v-slot:activator>
      <v-list-item-icon v-if="item.icon"><v-icon>{{item.icon}}</v-icon></v-list-item-icon>
      <v-list-item-title>{{item.label}}</v-list-item-title>
    </template>
    <MenuItem v-for="(item, key) in item.list" :key="key" :item="item"/>
  </v-list-group>
  <component v-else-if="item.type === 'component'" :is="componentData"/>
  <v-divider v-else/>
</template>

<script>
export default {
  name: 'MenuItem',
  props: ['item'],
  abstract: true,

  computed: {
    componentData() {
      return () => import(`@/render/components/SideBar/${this.item.component}.vue`);
    },

    click() {
      if (this.item.click) {
        return () => this.item.click(this);
      }

      return this.item.tab ? () => this.tabMgr.open(this.item.tab) : () => {};
    }
  }
}
</script>